body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body{
  font-family: 'Josefin Sans';
  font-size: 87%;
}

.product-list img{
  width: 6em;
  height: 6em;
}
.product-list li{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: .7em;
}
.product-list li button{
 padding: .3em .5em;
}
.bag-items li{
  padding: .7em;
  box-shadow: 0 4px 8px 0 rgba(208, 208, 0, 1), 0 6px 20px 0 rgba(208, 208, 0, .6);
  width: 70%;
  margin: 0 auto;
}
.bag-list li img{
  width: 4em;
  height: 4em;
  
}
.cart-counter{
  display: inline-block;
  position: relative;
  width: 3em;
}
.cart-counter span{
  position: absolute;
  left: 25%;
  top: 5%;
  width: 40%;
  font-weight: 800;
}
.proceed-btn{background-color: rgb(191, 247, 247);margin-right: 4%;}
.purchase-form input{
  float: right;
}
.purchase-form ul{
  width: 17em;
  margin: 0 auto;
}
.purchase-form li{
  margin-bottom: .6em;
}
.main-body-container{position: relative; min-height: 100vh;}
.main-body-container footer{position: absolute; top:100%;left: 0}
@media screen and (max-width: 772px){
  .bag-items li{
    padding: .7em;
    box-shadow: 0 4px 8px 0 rgba(208, 208, 0, 1), 0 6px 20px 0 rgba(208, 208, 0, .6);
    width: 100%;
    margin: 0 auto;
  }
  body{
    font-size: 80%;
  }
}